import React, { useEffect, useRef, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import styled from "styled-components";

import PageContainer from "../../utils/PageContainer";
import Loading from "../../utils/fetchUtils/Loading";
import ProductDescription from "./ProductDescription";
import RatingReviews from "./RatingReviews";
import RelatedProducts from "./RelatedProducts";
import ProductImageSlider from "./ProductImageSlider";
import ProductHandler from "./ProductHandler";
import { useGetProductQuery } from "../../../redux/api/products/productsAPI";
import Error from "../../utils/fetchUtils/Error";

const ProductDetails = () => {
  const sliderRef = useRef();
  const dispatch = useDispatch();
  const [favourite, setFavourite] = useState(false);

  const { productId } = useParams();
  const { data, isLoading, isError } = useGetProductQuery(productId);
  console.log(data);

  useEffect(() => {
    setFavourite(false);

    function localFavouriteItem() {
      const favItems = JSON.parse(localStorage.getItem("favourite-item"));
      favItems &&
        favItems.find((item) => {
          if (item.product._id === productId) {
            setFavourite(true);
          }
          return null;
        });
    }
    localFavouriteItem();
  }, [dispatch, productId]);

  if (isLoading) {
    return <Loading />;
  }

  if (isError) {
    return Error;
  }

  return (
    <PageContainer>
      <ProductDetailsContainer>
        <ContainerTop>
          <SliderContainer>
            <ProductImageSlider
              productImages={data?.Product?.images}
              sliderRef={sliderRef}
            />
          </SliderContainer>
          <DetailsContainer>
            <ProductHandler
              Product={data.Product}
              favourite={favourite}
              setFavourite={setFavourite}
            />
          </DetailsContainer>
        </ContainerTop>
        <ProductDescription product={data.Product} />
        <RatingReviews product={data.Product} />
        <RelatedProducts category={data.Product.category} id={productId} />
      </ProductDetailsContainer>
    </PageContainer>
  );
};

export default ProductDetails;

const ProductDetailsContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1440px;
  margin-top: 2rem;

  @media screen and (max-width: 1440px) {
    padding: 0 2rem;
  }
`;
const ContainerTop = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;

  @media screen and (max-width: 768px) {
    flex-direction: column;
    margin-bottom: 2rem;
  }
`;
const SliderContainer = styled.div`
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: center;
  margin-bottom: 3rem;

  @media screen and (max-width: 768px) {
    flex-direction: column;
  }
`;

const DetailsContainer = styled.div`
  margin-left: 5rem;
  @media screen and (max-width: 768px) {
    width: 90vw;
  }
  @media screen and (max-width: 576px) {
    width: 100%;
    margin-left: 0rem;
  }
`;
